:root {
    --blog-bg: #fafafa;
    --blog-body-bg: white;
    --blog-text-primary: #000;
    --blog-text-secondary: #1a1a1a;
    --blog-text-muted: #737373;
    --blog-hover-bg: rgba(0, 0, 0, 0.04);
}

[data-theme="dark"] {
    --blog-bg: #1e1e1e;
    --blog-body-bg: #1e1e1e;
    --blog-text-primary: #e0e0e0;
    --blog-text-secondary: #cccccc;
    --blog-text-muted: #999999;
    --blog-hover-bg: rgba(255, 255, 255, 0.08);
}

body {
    background: var(--blog-body-bg);
    margin: 0;
    padding: 0;
}

.blog-page {
    display: flex;
    flex-direction: column;
    min-height: 100vh;
    background: var(--blog-bg);
    overflow-x: hidden;
}

.blog-content {
    flex: 1;
    display: flex;
    justify-content: center;
    padding: 6rem 2rem 8rem 2rem;
    padding-top: calc(6rem + 50px);
    width: 100%;
}

.blog-container {
    max-width: 680px;
    width: 100%;
    animation: fadeInUp 0.6s ease-out;
}

@keyframes fadeInUp {
    from {
        opacity: 0;
        transform: translateY(20px);
    }
    to {
        opacity: 1;
        transform: translateY(0);
    }
}

.year-section {
    margin-bottom: 2.5rem;
}

.year-title {
    font-size: 1.75rem;
    font-weight: 700;
    margin-bottom: 2.5rem;
    color: var(--blog-text-primary);
    letter-spacing: -0.02em;
}

.posts-list {
    display: flex;
    flex-direction: column;
    gap: 0;
}

.post-item {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    padding: 1rem 1.25rem 1rem 0;
    margin: 0.25rem 0;
    text-decoration: none;
    transition: all 0.15s ease;
    position: relative;
    border-radius: 12px;
}

.post-item:hover {
    background: var(--blog-hover-bg);
    padding-left: 1.25rem;
}

.post-item:hover .post-title {
    color: var(--blog-text-primary);
}

.post-title {
    font-size: 1.05rem;
    font-weight: 600;
    color: var(--blog-text-secondary);
    text-transform: lowercase;
    letter-spacing: -0.01em;
    transition: color 0.15s ease;
}

.post-date {
    font-size: 0.9rem;
    font-weight: 400;
    color: var(--blog-text-muted);
    white-space: nowrap;
    margin-left: 2rem;
    text-transform: lowercase;
}

@media (max-width: 768px) {
    .blog-content {
        padding: 4rem 1.5rem 7rem 1.5rem;
    }
    
    .year-section {
        margin-bottom: 2rem;
    }
    
    .year-title {
        font-size: 1.5rem;
        margin-bottom: 2rem;
    }
    
    .post-item {
        flex-direction: column;
        align-items: flex-start;
        gap: 0.25rem;
        padding: 0.875rem 1rem 0.875rem 0;
        margin: 0.15rem 0;
    }
    
    .post-item:hover {
        padding-left: 1rem;
    }
    
    .post-title {
        font-size: 0.95rem;
        font-weight: 600;
    }
    
    .post-date {
        font-size: 0.85rem;
        margin-left: 0;
        color: #999;
    }
}

